


































































import Vue from 'vue'
export default Vue.extend({
  props: {
    value: {
      type: Object,
      required: true,
    },
    title: {
      type: String,
      required: true,
    },
    icon: {
      type: String,
      required: false,
    },
    open: {
      type: Boolean,
      default: false,
    },
    subGroup: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      list: {} as any,
    }
  },
  watch: {
    value: {
      deep: true,
      immediate: true,
      handler(v) {
        this.list = v
      },
    },
    list: {
      deep: true,
      immediate: true,
      handler(v) {
        this.$emit('input', v)
      },
    },
  },
})
