











































import DataGeneratorPanel from '@/components/DataGeneratorPanel.vue'
import GraphOptionsPanel from '@/components/GraphOptionsPanel.vue'
import SigmaGraph from '@/components/SigmaGraph.vue'
import Vue from 'vue'
import { IGraphFilters, IGraphSettings, IQueryResult } from './interfaces'
import { calcAndSetDepth } from './GraphMetricsCalc'
import InfoPanel from './InfoPanel.vue'
import DataSheet from './DataSheet.vue'
export default Vue.extend({
  created() {
    const { graphdata } = window as any
    if (graphdata) {
      this.onShowClicked(graphdata)
      this.justGraph = true
    }
  },
  components: {
    SigmaGraph,
    // DataGeneratorPanel,
    GraphOptionsPanel,
    InfoPanel,
    // DataSheet,
  },
  data() {
    return {
      graphBus: new Vue(),
      queryResult: null as null | IQueryResult,
      openOptions: false,
      openInfo: false,
      infoPanelData: {} as any,
      centerData: {} as any,
      justGraph: false,

      graphSettings: {
        layout: {
          editable: true,
          enable: true,
          type: 'enum-eq',
          options: ['forceatlas2', 'force'],
          value: 'forceatlas2',
        },
        nodeTooltipCss: {
          editable: false,
          enable: true,
          type: 'other',
          value: {},
        },
        nodeLabelStyle: {
          enable: true,
          editable: false,
          type: 'other',
          value: { fixed: true, size: 10, color: 'blue' },
        },
        coloring: {
          enable: true,
          editable: true,
          type: 'enum-eq',
          options: ['default'],
          value: 'default',
        },
        sizing: {
          enable: true,
          editable: true,
          type: 'enum-eq',
          options: ['default'],
          value: 'default',
        },
      } as IGraphSettings,
      graphFilters: {
        nodeMetrics: {
          enable: true,
          editable: true,
          type: 'object',
          children: {
            degree: {
              enable: true,
              editable: true,
              type: 'range',
              options: [0, 15],
              value: [0, 15],
            },
            inDegree: {
              enable: false,
              editable: true,
              type: 'range',
              options: [1, 10],
              value: [1, 10],
            },
            outDegree: {
              enable: false,
              editable: true,
              type: 'range',
              options: [1, 10],
              value: [1, 10],
            },
            // cluster: {
            // enable: false,
            //   editable: true,
            //   type: 'list',
            //   options:[1,2,3,4,5],
            //   value:[1,2,3,4]
            // },
            // closeness: {
            //   enable: false,
            //   editable: true,
            //   type: 'range',
            //   options: [1, 5],
            //   value: [1, 5],
            // },
          },
        },
        depth: {
          enable: true,
          editable: true,
          type: 'enum-leq',
          options: [1, 2, 3],
          value: 1,
        },
      } as IGraphFilters,
    }
  },
  computed: {
    graphUserOptions: {
      get(): any {
        return {
          settings: this.graphSettings,
          filters: this.graphFilters,
        }
      },
      set(options: any) {
        this.graphSettings = options.settings
        this.graphFilters = options.filters
      },
    },
  },
  methods: {
    getVariablesDircetValue(variables: IGraphSettings | IGraphFilters) {
      if (!variables) return variables
      const direct: any = {}
      Object.entries(variables).forEach(([key, val]) => {
        if (val.type != 'object') {
          direct[key] = val.value
        } else {
          direct[key] = this.getVariablesDircetValue(val.children)
        }
      })
      return direct
    },
    onNodeClick({ id, data }: any) {
      this.openOptions = false
      this.infoPanelData = data
      this.openInfo = true
    },
    onShowClicked(queryResult: IQueryResult) {
      this.queryResult = queryResult
      const { graph, table, user } = this.queryResult

      const maxDegree = Object.values(graph.nodes).reduce(
        (pre, cur) => Math.max(pre, cur.degree || 1),
        0
      )
      const maxInDegree = Object.values(graph.nodes).reduce(
        (pre, cur) => Math.max(pre, cur.inDegree || 1),
        0
      )
      const maxOutDegree = Object.values(graph.nodes).reduce(
        (pre, cur) => Math.max(pre, cur.outDegree || 1),
        0
      )
      const metrics = this.graphFilters.nodeMetrics.children
      metrics['degree'].options = [0, maxDegree]
      metrics['degree'].value = [0, maxDegree]
      metrics['inDegree'].options = [0, maxInDegree]
      metrics['inDegree'].value = [0, maxInDegree]
      metrics['outDegree'].options = [0, maxOutDegree]
      metrics['outDegree'].value = [0, maxOutDegree]

      this.graphSettings = { ...this.graphSettings, ...graph.meta.settings }
      this.graphFilters = { ...this.graphFilters, ...graph.meta.filters }

      if (!user.hasAccess) {
        alert(
          `Access limit,
          result count is more than ${user.nodeCountLimit} nodes.
          you can change filters to reduce counts.`
        )
      }

      const centerkey = graph.meta.centerNodeId
      calcAndSetDepth(graph, centerkey)
      this.infoPanelData = graph.nodes[centerkey]
      this.centerData = graph.nodes[centerkey]

      this.graphBus.$emit('drawGraph', graph)
    },
  },
})
