

































import Vue from 'vue'
import {
  IGraphFilters,
  IGraphSettings,
  IVariableProperties,
} from './interfaces'
import SubList from './SubList.vue'
export default Vue.extend({
  components: { SubList },
  props: {
    value: {
      type: Object,
      required: true,
    },
    open: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      settings: {} as IGraphSettings,
      filters: {} as IGraphFilters,
      lastSettings: {} as IGraphSettings,
      lastFilters: {} as IGraphFilters,
      isOpen: false,
      applyClicked: false,
    }
  },
  watch: {
    value: {
      deep: true,
      immediate: true,
      handler(o) {
        if (o && Object.keys(o).length > 0) {
          this.settings = JSON.parse(JSON.stringify(o.settings))
          this.lastSettings = JSON.parse(JSON.stringify(o.settings))
          this.filters = JSON.parse(JSON.stringify(o.filters))
          this.lastFilters = JSON.parse(JSON.stringify(o.filters))
        }
      },
    },
    open(val) {
      this.isOpen = val
    },
    isOpen(val) {
      if (!val && !this.applyClicked) {
        this.closedWithoutApply()
        this.applyClicked = false
      }
      if (!val) {
        this.$emit('close')
      }
    },
  },
  computed: {
    width(): string {
      let w = '40vw'
      if (this.$vuetify.breakpoint.smAndDown) w = '80vw'
      return w
    },
  },
  methods: {
    applyOptions() {
      this.lastSettings = JSON.parse(JSON.stringify(this.settings))
      this.lastFilters = JSON.parse(JSON.stringify(this.filters))
      this.$emit('input', { settings: this.settings, filters: this.filters })
      this.applyClicked = true
      this.isOpen = false
    },
    closedWithoutApply() {
      this.settings = JSON.parse(JSON.stringify(this.lastSettings))
      this.filters = JSON.parse(JSON.stringify(this.lastFilters))
    },
  },
})
