import { IGraphData } from './interfaces'

function setDepthForConnected(
  graphData: IGraphData,
  nodeIds: string[],
  depth: number
): string[] {
  const { nodes, edges } = graphData
  const changes: string[] = []
  let fromIncludes = false
  let toIncludes = false
  Object.values(edges)
    .filter((val) => val.depth !== 0 && !val.depth)
    .forEach((val) => {
      fromIncludes = nodeIds.includes(val.from)
      toIncludes = nodeIds.includes(val.to)
      if (fromIncludes && toIncludes) {
        val.depth = depth - 1
      } else if (fromIncludes || toIncludes) {
        val.depth = depth
        if (fromIncludes && !nodes[val.to].depth) {
          nodes[val.to].depth = depth
          changes.push(val.to)
        } else if (toIncludes && !nodes[val.from].depth) {
          nodes[val.from].depth = depth
          changes.push(val.from)
        }
      }
    })
  return changes
}
export function calcAndSetDepth(graphData: IGraphData, centerkey: string) {
  const { nodes, edges } = graphData
  let depth = 0
  nodes[centerkey].depth = 0
  let changes = [centerkey]
  while (changes.length > 0) {
    depth++
    changes = setDepthForConnected(graphData, changes, depth)
  }

  //this nodes are not connected to center node, so depth is infinite
  Object.values(edges)
    .filter(({ depth }) => depth !== 0 && !depth)
    .forEach((val) => (val.depth = 9999))
  Object.values(nodes)
    .filter(({ depth }) => depth !== 0 && !depth)
    .forEach((val) => (val.depth = 9999))
}
