


































import Vue from 'vue'
export default Vue.extend({
  props: {
    data: {
      type: Object,
      required: true,
    },
    open: {
      type: Boolean,
      default: false,
    },
  },
  watch: {
    open(val) {
      this.isOpen = val
    },
    isOpen(val) {
      if (!val) {
        this.$emit('close')
      }
    },
  },
  data() {
    return {
      isOpen: false,
    }
  },
  computed: {
    width(): string {
      let w = '30vw'
      if (this.$vuetify.breakpoint.smAndDown) w = '80vw'
      return w
    },
    categorized(): any {
      const info = this.data
      const categorizedData: any = {
        personalInfo: {
          title: 'PersonalInfo',
          icon: 'mdi-account',
          children: {},
        },
        location: {
          title: 'Location',
          icon: 'mdi-map-marker',
          children: {},
        },
      }
      categorizedData.personalInfo.children = {
        Name: info.name,
        Family: info.family,
        Father: info.father,
        GrandFather: info.grandFather,
        Trible: info.trible,
        BirthDate: info.birthDate,
      }
      categorizedData.location.children = {
        Citizen: info.citizen,
        BirthCity: info.birthCity,
        Address: info.location,
      }
      return categorizedData
    },
  },
})
