


























import Vue from 'vue'
import Sigma from 'sigma'
import getNodeProgramImage from 'sigma/rendering/webgl/programs/node.image'
import Graph, { MultiGraph } from 'graphology'
import { Attributes, SerializedGraph } from 'graphology-types'
import forceLayout from 'graphology-layout-force/worker'
import forceAtlas2 from 'graphology-layout-forceatlas2'
import forceAtlas2Layout from 'graphology-layout-forceatlas2/worker'
import NoverlapLayout from 'graphology-layout-noverlap/worker'
import { EdgeDisplayData, NodeDisplayData } from 'sigma/types'
import { Settings } from 'sigma/settings'
import {
  IGraphData,
  IGraphFilters,
  IGraphSettings,
  INode,
  IVariableProperties,
} from './interfaces'
type graphImportData = Partial<
  SerializedGraph<Attributes, Attributes, Attributes>
>
export default Vue.extend({
  created() {
    if (this.$props.bus) {
      this.$props.bus.$on('drawGraph', this.drawAndShowGraph)
      this.$props.bus.$on('setSelection', this.selectNodesEdges)
    }
  },
  mounted() {
    this.container = document.getElementById('sigma-container') as HTMLElement
    this.tooltipEL = document.getElementById('nodeTooltip') as HTMLElement
  },
  props: {
    bus: {
      type: Object,
      required: true,
    },
    settings: {
      type: Object,
      required: true,
    },
    filters: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      container: (null as unknown) as HTMLElement,
      tooltipEL: (null as unknown) as HTMLElement,
      graphData: { nodes: {}, edges: {}, meta: {} } as IGraphData,
      filteredNodes: [] as any[],
      filteredEdges: [] as any[],
      graph: new Graph(),
      sigma: (null as unknown) as Sigma,
      sigmaSettings: {
        enableEdgeClickEvents: true,
        enableEdgeHoverEvents: 'debounce',
        renderEdgeLabels: false,
        nodeProgramClasses: {
          image: getNodeProgramImage(),
        },
        allowInvalidContainer: true,
      } as Partial<Settings>,
      layout: null as any,
      draggedNode: '',
      hoveredNode: '',
      hoveredNodeHasTooltip: false,
      hoveredEdge: '',
      hoveredNeighbors: null as null | Set<string>,

      isDraggingNode: false,
      doubleClicked: false,

      drawParams: {
        //
      },

      lastHighlightReq: null as null | {
        nodeIds: string[]
        edgeIds: string[]
      },

      nodeTooltipBaseStyle: {
        position: 'absolute',
        top: 0,
        left: 0,
        border: '1px solid black',
        'border-radius': '4px',
        'background-color': 'khaki',
        color: 'black',
      } as any,
      nodeTooltipCss: {} as any,
    }
  },
  watch: {
    settings: {
      deep: true,
      immediate: true,
      handler(n, pre) {
        this.updateSettings(n, pre)
      },
    },
    filters: {
      deep: true,
      immediate: true,
      handler(n, pre) {
        this.updateFilters(n, pre)
      },
    },
  },
  methods: {
    drawAndShowGraph(graphData: IGraphData) {
      this.graphData = graphData

      this.importGraphData(graphData)

      this.assignLayout()

      this.showGraphAndAddListeners()
    },
    assignLayout() {
      if (!this.graph) return

      if (this.layout)
        try {
          this.layout.kill()
        } catch {
          //
        }

      switch (this.settings.layout.value) {
        case 'noverlap':
          this.layout = new NoverlapLayout(this.graph, {
            settings: {
              //
            },
          })
          break
        case 'force':
          this.layout = new forceLayout(this.graph)
          break
        case 'forceatlas2':
        default:
          this.layout = new forceAtlas2Layout(this.graph, {
            settings: {
              ...forceAtlas2.inferSettings(this.graph),
              // scalingRatio: 100,
              // gravity: 0,
              // strongGravityMode: false,
              // slowDown: 1,
            },
          })

        //or sync with iteration
        // forceAtlas2.assign(this.graph, { orceAtlas2.inferSettings(this.graph), iterations: 300 })
      }

      //start, wait to arrange and stop
      this.layout.start()
      setTimeout(() => {
        this.layout.stop()
      }, Math.max(this.graph.order * 10, 3000))
    },
    showGraphAndAddListeners() {
      if (this.sigma) {
        this.sigma.refresh()
        return
      }

      if (this.settings.nodeLabelStyle) {
        const { fixed, size, color } = this.settings.nodeLabelStyle.value
        this.$set(this.sigmaSettings, 'labelColor', { color })
        this.$set(this.sigmaSettings, 'labelSize', size)
        this.$set(
          this.sigmaSettings,
          'labelRenderedSizeThreshold',
          fixed ? 0 : 6
        )
      }
      this.sigma = new Sigma(this.graph, this.container, this.sigmaSettings)

      this.addClickEvents()

      this.supportNodesDrag()
      this.supportNeighbourColoringOnHover()
    },
    checkFilters(nodeEntries: any[], filters: IGraphFilters) {
      let filteredEntries = nodeEntries
      Object.entries(filters).forEach(([key, props]) => {
        if (props.enable) {
          switch (props.type) {
            case 'range':
              filteredEntries = filteredEntries.filter(
                ([id, n]) =>
                  n[key] >= props.value[0] && n[key] <= props.value[1]
              )
              break
            case 'enum-leq':
              filteredEntries = filteredEntries.filter(
                ([id, n]) => n[key] <= props.value
              )
              break
            case 'string':
              filteredEntries = filteredEntries.filter(
                ([id, n]) => n[key] == props.value
              )
              break
            case 'boolean':
              filteredEntries = filteredEntries.filter(
                ([id, n]) => n[key] == props.enable || props.value
              )
              break
            case 'object':
              filteredEntries = this.checkFilters(
                filteredEntries,
                props.children
              )
              break
          }
        }
      })

      return filteredEntries
    },
    importGraphData(graphData: IGraphData) {
      if (this.graph)
        try {
          this.graph.clear()
        } catch {
          //
        }

      const { nodes, edges } = graphData
      const centerNodeId = graphData.meta.centerNodeId
      const graphOrder = Object.keys(nodes).length
      const angleRatio = (2 * Math.PI) / graphOrder
      let imageSrc = undefined as any

      this.filteredNodes = this.checkFilters(
        Object.entries(nodes),
        this.filters
      )
      ///keep central Node different and persist
      nodes[centerNodeId].color = 'cyan'
      if (!this.filteredNodes.find(([id, _]) => id === centerNodeId)) {
        this.filteredNodes.push([centerNodeId, nodes[centerNodeId]])
      }
      ///
      const filteredNodesIds = this.filteredNodes.map(([id, _]) => id)
      this.filteredEdges = Object.entries(edges).filter(
        ([_, e]) =>
          filteredNodesIds.includes(e.from) && filteredNodesIds.includes(e.to)
      )

      const importData: graphImportData = {
        nodes: this.filteredNodes.map(([id, n], i) => {
          const angle = i * angleRatio
          return {
            key: id,
            attributes: {
              color: n.color,
              size: n.size,
              label: n.label,
              type: 'image',
              x: 100 * Math.cos(angle),
              y: 100 * Math.sin(angle),
              image: imageSrc
                ? imageSrc
                : (imageSrc = require('@/assets/images/account.svg')),
            },
          }
        }),
        edges: this.filteredEdges.map(([id, e]) => ({
          key: id,
          source: e.from,
          target: e.to,
          attributes: {
            size: e.size,
            color: e.color,
            label: e.label,
            type: 'arrow',
          },
        })),
      }

      this.graph.import(importData)
    },

    addClickEvents() {
      this.sigma.on('clickNode', ({ node }) => {
        setTimeout(() => {
          if (this.doubleClicked) {
            this.doubleClicked = false
            return
          }
          this.selectNodesEdges({ nodeIds: [node], edgeIds: [] })
          this.$emit('nodeClick', {
            id: node,
            data: this.graphData.nodes[node],
          })
        }, 200)
      })
      // this.sigma.on('rightClickNode', ({node})=>{
      //   this.selectNodesEdges({ nodeIds: [node], edgeIds: [] })
      //   this.$emit('nodeRightClick', node)
      // })
      this.sigma.on('doubleClickNode', (e) => {
        this.doubleClicked = true
        this.selectNodesEdges({ nodeIds: [e.node], edgeIds: [] })
        this.$emit('nodeDoubleClick', e.node)
        //imp in parent
        this.onNodeDoubleClick(e.node)

        e.preventSigmaDefault() //prevent camera zoom
      })
    },
    supportNeighbourColoringOnHover() {
      this.sigma.on('enterNode', ({ node }) => {
        this.setHoveredNode(node)
        const { tooltip } = this.graphData.nodes[node]
        this.hoveredNodeHasTooltip = tooltip != (null || undefined)
        if (this.hoveredNodeHasTooltip) {
          const { x, y } = this.graph.getNodeAttributes(node)
          const { x: viewX, y: viewY } = this.sigma.graphToViewport({ x, y })
          this.nodeTooltipBaseStyle.top = viewY + 15 + 'px'
          this.nodeTooltipBaseStyle.left = viewX + 'px'
          this.tooltipEL.innerHTML = tooltip || ''
        }
      })
      this.sigma.on('leaveNode', () => {
        this.setHoveredNode('')
      })

      // Render nodes accordingly to the internal state:
      // 1. If a node is selected, it is highlighted
      // 2. If there is a hovered node, all non-neighbor nodes are greyed
      // 3. If there is query, all non-matching nodes are greyed
      this.sigma.setSetting('nodeReducer', (node, data) => {
        const res: Partial<NodeDisplayData> = { ...data }

        if (
          this.hoveredNeighbors &&
          !this.hoveredNeighbors.has(node) &&
          this.hoveredNode !== node
        ) {
          res.label = ''
          ;(res as any).image = ''
          res.color = '#f6f6f6' //'#c0c0c0'
        }

        // if (this.selectedNode === node) {
        //   res.highlighted = true;
        // } else if (this.suggestions && !this.suggestions.has(node)) {
        //   res.label = "";
        //   res.color = "#f6f6f6";
        // }

        return res
      })

      // Render edges accordingly to the internal state:
      // 1. If a node is hovered, the edge is hidden if it is not connected to the
      //    node
      // 2. If there is a query, the edge is only visible if it connects two
      //    suggestions
      this.sigma.setSetting('edgeReducer', (edge, data) => {
        const res: Partial<EdgeDisplayData> = { ...data }

        if (
          this.hoveredNode &&
          !this.graph.hasExtremity(edge, this.hoveredNode)
        ) {
          res.color = '#f6f6f6' // '#d0d0d0'
        }

        // if (this.suggestions && (!this.suggestions.has(this.graph.source(edge)) || !this.suggestions.has(this.graph.target(edge)))) {
        //   res.hidden = true;
        // }

        return res
      })
    },
    setHoveredNode(node: string) {
      this.hoveredNode = node
      if (node) {
        this.hoveredNeighbors = new Set(this.graph.neighbors(node))
      } else {
        this.hoveredNeighbors = null
      }

      // Refresh rendering:
      this.sigma.refresh()
    },
    supportNodesDrag() {
      this.sigma.on('clickStage', (e) => {
        this.removeSelectedNodesEdges()
        this.$emit('spaceClick')
      })
      this.sigma.on('downNode', (e) => {
        const nodeId = e.node

        if (
          this.lastHighlightReq &&
          !this.lastHighlightReq.nodeIds.includes(nodeId)
        ) {
          this.removeSelectedNodesEdges()
          this.$emit('spaceClick')
        }

        const edgeIds = this.filteredEdges
          .filter(([_, e]) => e.from == nodeId || e.to == nodeId)
          .map(([id, _]) => id)
        this.selectNodesEdges({ nodeIds: [nodeId], edgeIds })
        this.isDraggingNode = true
        this.draggedNode = e.node
        this.graph.setNodeAttribute(e.node, 'highlighted', true)
        this.sigma.getCamera().disable()
      })
      this.sigma.getMouseCaptor().on('mousemovebody', (e) => {
        if (!this.isDraggingNode || !this.draggedNode) return

        const nodeNewPos = this.sigma.viewportToGraph(e)
        this.graph.setNodeAttribute(this.draggedNode, 'x', nodeNewPos.x)
        this.graph.setNodeAttribute(this.draggedNode, 'y', nodeNewPos.y)

        // Prevent sigma to move camera:
        // e.preventSigmaDefault();
        // e.original.preventDefault();
        // e.original.stopPropagation();
      })
      this.sigma.getMouseCaptor().on('mouseup', () => {
        if (this.draggedNode) {
          this.graph.removeNodeAttribute(this.draggedNode, 'highlighted')
        }
        this.isDraggingNode = false
        this.draggedNode = ''
        this.sigma.getCamera().enable()
      })

      // Disable the autoscale at the first down interaction
      this.sigma.getMouseCaptor().on('mousedown', () => {
        if (!this.sigma.getCustomBBox())
          this.sigma.setCustomBBox(this.sigma.getBBox())
      })
    },
    selectNodesEdges(list: { nodeIds: string[]; edgeIds: string[] }) {
      this.removeSelectedNodesEdges()
      if (!list) return
      const { nodeIds, edgeIds } = list
      nodeIds.forEach((id) => {
        this.graph.setNodeAttribute(id, 'highlighted', true)
      })
      edgeIds.forEach((id) =>
        this.graph.setEdgeAttribute(id, 'color', '#cc0000')
      )
      this.lastHighlightReq = list
    },
    removeSelectedNodesEdges() {
      if (!this.lastHighlightReq) return
      const { edgeIds, nodeIds } = this.lastHighlightReq
      nodeIds.forEach((id) => {
        this.graph.removeNodeAttribute(id, 'highlighted')
      })
      edgeIds.forEach((id) =>
        this.graph.setEdgeAttribute(id, 'color', this.graphData.edges[id].color)
      )
      this.lastHighlightReq = null
    },
    onZoom(step: number) {
      try {
        if (this.sigma) {
          if (step > 0) this.sigma.getCamera().animatedZoom({ duration: 500 })
          else if (step < 0)
            this.sigma.getCamera().animatedUnzoom({ duration: 500 })
        }
      } catch {
        //
      }
    },

    onNodeDoubleClick(id: string) {
      const link = this.graphData.nodes[id].link
      if (link) window.open(link, '_blank')
    },

    onLayoutPlayStopClick(): void {
      if (!this.layout) return
      if (this.layout.isRunning()) {
        this.layout.stop()
      } else {
        this.layout.start()
      }
    },

    updateSettings(newSettings: IGraphSettings, lastSettings: IGraphSettings) {
      if (!newSettings || Object.keys(newSettings).length == 0) return
      if (!this.sigma || !lastSettings) return // not initialized

      if (JSON.stringify(lastSettings) != JSON.stringify(newSettings)) {
        this.importGraphData(this.graphData)
        this.assignLayout()
        this.sigma.refresh()
      }

      // if (newSettings.layout && lastSettings.layout != newSettings.layout) {
      //   this.assignLayout()
      // }
      //...for all settings
    },
    updateFilters(newFilters: IGraphFilters, lastFilters: IGraphFilters) {
      if (!newFilters || Object.keys(newFilters).length == 0) return
      if (!this.sigma) return // not initialized

      if (JSON.stringify(lastFilters) != JSON.stringify(newFilters)) {
        this.importGraphData(this.graphData)
        this.assignLayout()
        this.sigma.refresh()
      }

      // const { depth } = lastFilters
      // if (newFilters.depth && depth != newFilters.depth) {
      //   this.importGraphData(this.graphData)
      //   this.assignLayout()
      //   this.sigma.refresh()
      // }
      //...for all filters
    },
  },
})
